import React from "react";
import { useLightMode } from "./ModeContext";
import Title from "./Title";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from "chart.js";
import { Radar } from "react-chartjs-2";

ChartJS.register(RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend);

function Chartcnt() {
  const { isLightMode } = useLightMode();
  const option = {
    elements: {
      // .
      line: {
        borderWidth: 2,
        borderColor: "rgba(255, 99, 132, 1)",
      },
      // .
      point: {
        pointBackgroundColor: "rgba(50, 115, 208, 1)",
      },
    },
    scales: {
      r: {
        ticks: {
          stepSize: 1,
          display: false,
        },
        grid: {
          color: "rgb(100, 100, 100)",
        },
        //  .
        pointLabels: {
          font: {
            size: 18,
            weight: "700",
            family: "Pretendard",
          },
          color: "rgba(50, 115, 208, 1)",
        },
        angleLines: {
          display: false,
        },
        suggestedMin: 0,
        suggestedMax: 10,
      },
    },
    //    label  .
    plugins: {
      legend: {
        display: false,
      },
    },
    //     .
    animation: {
      duration: 0,
    },
  };
  const data = {
    labels: [
      " ",
      "",
      " ",
      "",
      "",
      "",
      "",
      "",
      "",
    ],
    datasets: [
      {
        label: "It's me",
        data: [10, 1, 10, 8, 9, 10, 7, 9, 8],
        backgroundColor: "rgba(142, 208, 243, 0.5)",
        borderColor: "rgba(50, 115, 208, 1)",
        borderWidth: 1,
      },
    ],
  };
  return (
    <div id="chart" className={"subject_wrap " + (isLightMode ? "light" : "dark")}>
      <Title title={"   "} />
      <Radar data={data} options={option}></Radar>
    </div>
  );
}

export default Chartcnt;
